import React, { Component } from 'react'
import {Link} from 'react-router-dom'

class Header extends Component {

  render() {
    var currentPath = this.props.location.pathname.split("/")[1]
    return (
        <header className="App-header">
          <Link to="/" className={ currentPath +" Header-logo"}>
            <h1 className="App-title"><span>{this.props.title !== "Home" ? this.props.title : "?018" }</span></h1>
            <h2>Komunálne voľby {this.props.location.pathname !== "/" && this.props.title !== "Home" && "2018"}</h2>
          </Link>
          <nav>
            <ul>
              <li className={"tab zvolen" + (currentPath === "zvolen" ? " active" : "")}><Link to="/zvolen">Zvolen</Link></li>
              <li className={"tab bzovik" + (currentPath === "bzovik" ? " active" : "")}><Link to="/bzovik">Bzovík</Link></li>
              <li className={"tab sv-anton-povraznik" + (currentPath === "sv-anton-povraznik" ? " active" : "")}><Link to="/sv-anton-povraznik">Sv.Anton / Povrazník</Link></li>
              {/* <li className={"tab jelsava" + (currentPath === "jelsava" ? " active" : "")}><Link to="/jelsava">Jelšava</Link></li> */}
              <li className={"tab dobra_niva" + (currentPath === "dobra_niva" ? " active" : "")}><Link to="/dobra_niva">Dobrá Niva</Link></li>
            </ul>
          </nav>
        </header>
    );
  }
}

export default Header;



